				}
				.page-homeList-sr-sssr-box{
					border:1px solid #f5f5f5;
					overflow: auto;
					.page-homeList-sr-sssr-item {
						width: 33.1%;
						float: left;
						margin: 0;
						padding: 0.2rem 0;
						border-right: 0.01rem solid #f5f5f5;
						border-bottom: 0.01rem solid #f5f5f5;
						&:nth-child(3n+3) {
							border-right: none;
						}
						dl {
							dt{
								line-height: 0.45rem;
							}
							dd {
								font-size: $font-size-littlebig;
								color: #333;
							}
						}
						.iconfont {
							font-size: $font-size-h3;
						}
					}
				}
				/*      .page-homeList-sr-sssr-box::-webkit-scrollbar{
								display: none;
							}*/
			}
			.page-homeList-sr-qtsr{
				margin-top:0.1rem;
			}
		}
	}
</style>
<template>
	<div class="page-homeList">
		<div class="page-homeList-scroll">
			<mt-swipe :auto="4000">
				<mt-swipe-item v-for="(item ,index) in bannerBox" :key="index"><img :src="item.imgUrl" /></mt-swipe-item>
			</mt-swipe>
		</div>
		<div class="page-homeList-sr">
			<div class="page-homeList-sr-sssr">
				<div class="page-homeList-sr-sssr-title">
					<span></span>
				</div>
				<div class="page-homeList-sr-sssr-box">
					<div class="page-homeList-sr-sssr-item el-center"  v-for="(item, index) in sssrList" :key="index" @click="turnNext(item.url)">
						<dl>
							<dt><i class="iconfont" :class="item.iconClass"></i></dt>
							<dd>{{item.name}} </dd>
						</dl>
					</div>
				</div>
			</div>
			<div class="page-homeList-sr-qtsr">
				<div class="page-homeList-sr-sssr-title">
					<span></span>
				</div>
				<div class="page-homeList-sr-sssr-box">
					<div class="page-homeList-sr-sssr-item el-center"  v-for="(item, index) in fssrList" :key="index" @click="turnNext(item.url)">
						<dl>
							<dt><i class="iconfont" :class="item.iconClass"></i></dt>
							<dd>{{item.name}} </dd>
						</dl>
					</div>
				</div>
			</div>
			<div class="page-homeList-sr-qtsr">
				<div class="page-homeList-sr-sssr-title">
					<span></span>
				</div>
				<div class="page-homeList-sr-sssr-box">
					<div class="page-homeList-sr-sssr-item el-center"  v-for="(item, index) in zsyzList" :key="index" @click="turnNext(item.url)">
						<dl>
							<dt><i class="iconfont" :class="item.iconClass"></i></dt>
							<dd>{{item.name}} </dd>
						</dl>
					</div>
				</div>
			</div>
			<div class="page-homeList-sr-qtsr">
				<div class="page-homeList-sr-sssr-title">
					<span></span>
				</div>
				<div class="page-homeList-sr-sssr-box">
					<div class="page-homeList-sr-sssr-item el-center"  v-for="(item, index) in cyfcList" :key="index" @click="turnNext(item.url)">
						<dl>
							<dt><i class="iconfont" :class="item.iconClass"></i></dt>
							<dd>{{item.name}} </dd>
						</dl>
					</div>
				</div>
			</div>
		</div>  </div>
</template>
<script>
import { Swipe, SwipeItem } from 'mint-ui';
import Vue from 'vue';
import ddApi from '../commons/ddApi';
import { session } from '../commons/services';
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
let config = '';
if (process.env.NODE_ENV === 'development') {
  config = __config.dev;
} else {
  config = __config.Default;
}
export default {
  name: 'czy-homeList',
  data () {
    return {
      selected: '1',
      bannerBox: [],
      corpId: '',
      userCode: '',
      ddConfig: {},
      srMenu: [],
      sssrList: [], // 
      fssrList: [], // 
      zsyzList: [], // 
      cyfcList: [] // 
    };
  },
  created () {
    this.getBg();
    /* this.getDdConfig().then(res => {
      dd.ready(() => {
        this.getUserCode();
      })
    }); */
    //    console.log(this.$global.currentUser)
    //    this.$global.currentUser = {corpId: '21221', userCode: '3333'};
    this.getMenu();
    //    this.getMenuConfig();
    //    this.getDdConfig();
  },
  methods: {
    getBg () {
      //      this.$http.get('/static/data/sr.json')
      //        .then((res) => {
      //          this.bannerBox = res.data.banner;
      //        }).catch((msg) => {
      //
      //        });
      this.bannerBox = [
        {'imgUrl': (config.IS_USE_LOCAL_AJAX ? '../../../static' : '/czy') + '/imgs/sybanner.png'},
        {'imgUrl': (config.IS_USE_LOCAL_AJAX ? '../../../static' : '/czy') + '/imgs/sybanner.png'},
        {'imgUrl': (config.IS_USE_LOCAL_AJAX ? '../../../static' : '/czy') + '/imgs/sybanner.png'},
        {'imgUrl': (config.IS_USE_LOCAL_AJAX ? '../../../static' : '/czy') + '/imgs/sybanner.png'}
      ];
    },
    getDdConfig () {
      // js-api 
      return this.$http.get(MyUrl('/api/dingUserInfo?doType=getDingConfig')).then(res => {
        //        console.log(res)
        if (res.status == 200) {
          //          alert('')
          //          alert(JSON.stringify(dd))
          let response = res.body.retData;
          this.corpId = response.corpId;
          this.ddConfig = {
            agentId: response.agentid, // ID
            corpId: response.corpId, // ID
            timeStamp: response.timeStamp, // 
            nonceStr: response.nonceStr, // 
            signature: response.signature, // 
            // type:0,   //0jsapi,1jsapi0dingtalk.js0.8.3
            jsApiList: ddApi.ddApiArray
          };
        }
      })
    },
    getUserCode () {
      // userCode
      const self = this;
      return new Promise((resolve, reject) => {
        // 
        dd.runtime.info({
          onSuccess: function (result) {
            //            alert('' + JSON.stringify(result))
            window.ability = parseInt(result.ability.replace(/\./g, ''));
            if (!window.ability || window.ability < 5) {
              MyAlipayJSBridge('toast', {
                content: ' runtime.permission.requestAuthCode',
                type: 'fail',
                duration: 2000
              });
              // reject()
            }
          },
          onFail: function (err) {
            //            alert('' + JSON.stringify(err))
            console.log(err)
            /*            const signRequest = {
              corpId: this.corpId || '',
              agentName: 'undefined',
              noCache: true
            }; */
            // 
            return this.$http.get(MyUrl('/api/dingUserInfo?doType=getDingConfig')).then(res => {
              console.log(res)
              if (res.status == 200) {
                //                alert('')
                //                alert(JSON.stringify(dd))
                let response = res.body.retData;
                this.corpId = response.corpId;
                this.ddConfig = {
                  agentId: response.agentid, // ID
                  corpId: response.corpId, // ID
                  timeStamp: response.timeStamp, // 
                  nonceStr: response.nonceStr, // 
                  signature: response.signature, // 
                  // type:0,   //0jsapi,1jsapi0dingtalk.js0.8.3
                  jsApiList: ddApi.ddApiArray
                };
              }
            })
          }
        });
        // userCode
        dd.runtime.permission.requestAuthCode({
          corpId: self.corpId,
          onSuccess: function (info) {
            alert('info' + JSON.stringify(info))
            self.userCode = info.code;
            this.$global.currentUser = {corpId: this.corpId, userCode: self.userCode};
            session.setSessionUser(this.$global.currentUser)
            // dd
            resolve()
          },
          onFail: function (info) {
            /*         const signRequest = {
              corpId: this.corpId || '',
              agentName: 'undefined',
              noCache: true
            }; */
            // 
            alert('info' + JSON.stringify(info))
            return this.$http.get(MyUrl('/api/dingUserInfo?doType=getDingConfig')).then(res => {
              console.log(res)
              if (res.status == 200) {
                alert('')
                alert(JSON.stringify(dd))
                let response = res.body.retData;
                this.corpId = response.corpId;
                this.ddConfig = {
                  agentId: response.agentid, // ID
                  corpId: response.corpId, // ID
                  timeStamp: response.timeStamp, // 
                  nonceStr: response.nonceStr, // 
                  signature: response.signature, // 
                  // type:0,   //0jsapi,1jsapi0dingtalk.js0.8.3
                  jsApiList: ddApi.ddApiArray
                };
              }
            })
          }
        });
      })
    },
    turnNext (url) {
      this.$router.push({name: url})
    },
    getMenu () {
      //      this.$http.get('../../../../static/data/sr.json')
      //        .then((res) => {
      //          this.srMenu = res.data.sssrList;
      //          this.sssrList = this.srMenu[0].sssrMenu;
      //          this.qtList = this.srMenu[1].qtsrMenu;
      //        })
      this.srMenu = [
        {
          'name': '',
          'id': 'sssr',
          'menu': [
            {
              'name': '',
              'iconClass': 'icon-jijin',
              'url': 'czy-dzjk-dzjkchart',
              'bj': '1'
            },
            {
              'name': '',
              'iconClass': 'icon-tongji',
              'url': '',
              'bj': '2'
            },
            {
              'name': '',
              'iconClass': 'icon-dingdanchaxun',
              'url': 'czy-dhcxList-dhcx-dhlb',
              'bj': '3'
            },
            {
              'name': '',
              'iconClass': 'icon-3',
              'url': '',
              'bj': '4'
            },
            {
              'name': '',
              'iconClass': 'icon-qianyijihua',
              'url': '',
              'bj': '5'
            },
            {
              'name': '',
              'iconClass': 'icon-zhihangzhongdian',
              'url': 'page-zdsyList-zdsy-zdsylb',
              'bj': '6'
            },
            {
              'name': '',
              'iconClass': 'icon-loufang',
              'url': '',
              'bj': '7'
            },
            {
              'name': '',
              'iconClass': 'icon-jishushichang-copy',
              'url': '',
              'bj': '8'
            },
            {
              'name': '',
              'iconClass': 'icon-zhibiaojieguo',
              'url': '',
              'bj': '9'
            }
          ]
        },
        {
          'name': '',
          'id': 'fssr',
          'menu': []
        },
        {
          'name': '',
          'id': 'zsyz',
          'menu': []
        },
        {
          'name': '',
          'id': 'cyfc',
          'menu': [
            {
              'name': '',
              'iconClass': 'icon-xianjinliuliangbiao-selected',
              'url': '',
              'bj': '1'
            },
            {
              'name': '',
              'iconClass': 'icon-69',
              'url': '',
              'bj': '2'
            },
            {
              'name': '',
              'iconClass': 'icon-shenpi',
              'url': '',
              'bj': '3'
            }
          ]
        }
      ];
      for (let i = 0; i < this.srMenu.length; i++) {
        if (this.srMenu[i].id === 'sssr') { // 
          this.sssrList = this.srMenu[i].menu;
          this.addItem(this.sssrList.length, this.sssrList);
        } else if (this.srMenu[i].id === 'fzsr') {
          this.fssrList = this.srMenu[i].menu;
          this.addItem(this.fssrList.length, this.fssrList);
        } else if (this.srMenu[i].id === 'zsyz') {
          this.zsyzList = this.srMenu[i].menu;
          this.addItem(this.zsyzList.length, this.zsyzList);
        } else if (this.srMenu[i].id === 'cyfc') {
          this.cyfcList = this.srMenu[i].menu;
          this.addItem(this.cyfcList.length, this.cyfcList);
        }
      }
    },
    addItem (length, box) {
      let tc = {
        'name': '',
        'iconClass': '',
        'url': ''
      };
      if (length % 3 !== 0) { // 3  
        if (length < 3) {
          for (let i = 0; i < 3 - length; i++) {
            box.push(tc)
          }
        } else if (length > 3 && length < 6) {
          for (let j = 0; j < 6 - length; j++) {
            box.push(tc);
          }
        }
      }
    },
    getMenuConfig () {
      const signRequest = {
        code: this.$global.currentUser.userCode,
        corpid: this.$global.currentUser.corpId
      };
      this.$http.get(MyUrl('/czy/api/dingUserInfo/getDingConfig'), {params: signRequest}).then(res => {
        if (res.status == 200) {
          let response = res.data;
          console.log(response)
        }
      })
    }
  },
};
</script>



